/*тренеры*/
.trainer-list {
  padding-top: 0px; }

h2.trainer-list__header {
  margin-left: 105px;
  margin-top: 74px; }

div.trainer-list__carousel-box {
  width: 100%;
  height: 294px;
  margin-top: 51px;
  display: grid;
  grid-template-columns: auto 1160px auto; }

div.arrow {
  position: relative;
  width: auto;
  height: 100%; }

button.arrow__button {
  position: absolute;
  top: 135px;
  cursor: pointer;
  width: 24px;
  height: 24px;
  background-color: white;
  border-top: 1px solid #AFAFAF;
  border-left: 1px solid #AFAFAF;
  border-bottom: none;
  border-right: none; }

button.arrow__button:hover {
  border-top: 1px solid black;
  border-left: 1px solid black; }

button.arrow__button--left {
  left: 51px;
  transform: rotate(-45deg); }

button.arrow__button--right {
  left: 28px;
  transform: rotate(135deg); }

button.arrow__button--invisible {
  visibility: hidden; }

div.carousel-box__slider {
  width: 1160px;
  height: 100%;
  overflow: hidden; }

ul.slider__list {
  position: relative;
  list-style: none;
  height: 100%;
  width: 100%;
  transition: transform 0.2s ease-out; }

li.trainers__unit {
  display: block;
  position: absolute;
  height: 100%;
  width: 260px;
  margin-right: 40px;
  background-size: contain;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  z-index: 0; }

li.trainers__unit * {
  color: white; }

div.trainers-unit__overlay {
  padding-top: 54px;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: background-color 0.2s ease; }

div.trainers-unit__overlay:hover {
  background-color: rgba(16, 38, 84, 0.89); }

h4.trainers-description__name {
  position: relative;
  font-family: "Roboto Condensed 700";
  font-size: 18px;
  color: white;
  top: 193px;
  transition: position 0.3 ease; }

div.trainers-unit__overlay:hover > h4.trainers-description__name {
  position: unset; }

span.trainers-description__sport {
  visibility: hidden;
  position: relative;
  font-family: "Roboto Condensed 400";
  font-size: 16px;
  line-height: 24px;
  color: #B6BDCC;
  transition: visibility 0.2 ease; }

div.trainers-unit__overlay:hover > span.trainers-description__sport {
  visibility: visible; }

ul.trainers-description__list {
  visibility: hidden;
  font-family: "Roboto Condensed 400";
  height: 65%;
  list-style-type: disc;
  list-style-position: outside;
  text-align: left;
  font-size: 14px;
  line-height: 150%;
  padding-left: 71px;
  padding-right: 45px;
  margin-top: 32px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: visibility 0.2s ease; }

div.trainers-unit__overlay:hover > ul.trainers-description__list {
  visibility: visible; }

